@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comic+Neue&display=swap);
html,
body {
  box-sizing: border-box;
  margin: 0;
  font-family: "Comic Neue", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

section {
  z-index: 2;
  font-family: "Comic Neue", cursive;
  margin: auto;
  width: calc(100vw - (100vw - 100%));
  height: 100vh;
  display: flex;
  flex: auto;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  background-color: white;
}

article {
  z-index: 2;
  font-family: "Comic Neue", cursive;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  justify-content: space-evenly;
  transform: translateY(16vh);
}
h1 {
  font-size: 7vmin;
}
h2 {
  font-size: 2vmax;
}
h3 {
  font-size: 7vmin;
}
p {
  z-index: 2;
  padding-left: 15vw;
  padding-right: 15vw;
  font-size: 3.5vmin;
  line-height: 1.7;
  text-align: left;
}
.social-icon-list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: space-around;
  align-items: center;
}
.card-wrappers {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
}

.card {
  font-family: "Source Code Pro", monospace;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.3);
  transition: 0.3s;
  width: 30vmin;
  height: 30vmin;
  display: flex;
  flex: content;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
img {
  width: 100%;
  height: auto;
}
.img {
  width: 15vmin;
  height: 15vmin;
}
strong {
  font-size: 2.2vh;
}
.strong {
  /* width: 2rem; */
  padding-top: 10%;
}

.nav {
  z-index: 10;
  position: fixed;
  display: flex;
  flex: auto;
  flex-direction: row;
  align-items: center;
  align-content: center;
  justify-content: flex-end;
  width: 100%;
  height: 5vmax;
}
.nav-btn {
  font-family: "Open Sans Condensed", sans-serif;
  cursor: pointer;
  text-decoration: underline;
  font-size: 2.6vmin;
  padding: 0.6vh 0.1vw;
  margin: 0px;
}
.link {
  padding: 0px;
  margin: 1.5vw;
}

main {
  box-sizing: border-box;
  margin: 0;
  font-family: "Comic Neue", cursive;
}
.tshirt {
  z-index: 10;
  position: fixed;
  cursor: pointer;
  padding: 0px;
  transform: translate(1.5vw, 1.5vw)
}

